import { useState, useContext, useEffect } from "react";
import { IoCloseOutline, IoCartOutline } from "react-icons/io5";
import Button from "../UI/Button";
// import { FiShoppingCart } from "react-icons/fi";
import "./CartPanel.css";
import CartContext from "../../store/CartContext";
import CartItem from "./CartItem";

const CartPanel = (props) => {
  const CartCtx = useContext(CartContext);

  const { items } = CartCtx;

  const numberOfCartItems = items.reduce((currentNumber, item) => {
    return currentNumber + item.amount;
  }, 0);
  const [Show, setShow] = useState(false);

  // Fastest way to toggle an on and off button
  const toggleShowAndClose = () => setShow((prevShow) => !prevShow);

  const totalAmount = `${CartCtx.totalAmount.toFixed(2)}`;
  // const hasItems = CartCtx.items.length > 0;

  const cartItemRemoveHandler = (id) => {
    CartCtx.removeItem(id);
  };
  const cartItemAddHandler = (item) => {
    CartCtx.addItem({ ...item, amount: 1 });
  };

  const cartItem = (
    <ul>
      {CartCtx.items.map((item) => (
        <CartItem
          key={item.id}
          image={item.image}
          name={item.name}
          amount={item.amount}
          price={item.price}
          onRemove={cartItemRemoveHandler.bind(null, item.id)}
          onAdd={cartItemAddHandler.bind(null, item)}
        />
      ))}
    </ul>
  );

  const [hasItem, setHasItem] = useState(true);

  const [bumpingBtn, setBumpingBtn] = useState(false);

  const buttonBumping = `${"bg-none hover:opacity-75 inline duration-300 ease-in-out"}
  ${bumpingBtn ? "bump" : ""}`;

  useEffect(() => {
    if (items.length === 0) {
      setHasItem(true);
    } else {
      setBumpingBtn(true);
      setHasItem(false);
    }

    const timer = setTimeout(() => {
      setBumpingBtn(false);
    }, 300);

    return () => {
      clearTimeout(timer);
    };
  }, [items, totalAmount]);

  return (
    <>
      <button
        class={buttonBumping}
        type="button"
        data-bs-toggle="offcanvas"
        data-bs-target="#offcanvasRight"
        aria-controls="offcanvasRight"
        onClick={toggleShowAndClose}
      >
        <IoCartOutline size={21} className="inline mr-1.5 text-gray-500" />
        <h5 className="text-white py-1 px-2 rounded-lg text-sm inline cart-item-number">
          {numberOfCartItems}
        </h5>
      </button>

      <div
        show={Show}
        class="offcanvas offcanvas-end fixed bottom-0 flex flex-col max-w-full bg-white invisible bg-clip-padding shadow-sm outline-none transition duration-300 ease-in-out text-gray-700 top-0 right-0 border-none w-96"
        tabindex="-1"
        id="offcanvasRight"
        aria-labelledby="offcanvasRightLabel"
      >
        <div class="offcanvas-header flex items-center justify-between p-4 border-b">
          <h5
            class="offcanvas-title mb-0 leading-normal font-semibold flex justify-center items-center m-0 mb-0 ml-1.5 new-color-title"
            id="offcanvasRightLabel"
          >
            <IoCartOutline size={18} className="mr-2" />
            My Cart
          </h5>
          <button
            type="button"
            class="btn-close box-content w-4 h-4 p-2 -my-5 text-black border-none rounded-none opacity-50 focus:shadow-none focus:outline-none focus:opacity-100 hover:text-black hover:opacity-75 hover:no-underline"
            data-bs-dismiss="offcanvas"
            aria-label="Close"
            onClick={toggleShowAndClose}
          >
            <IoCloseOutline size={21} />
          </button>
        </div>
        <div class="offcanvas-body flex-grow p-4 overflow-y-auto mb-24">
          {hasItem && <p>You have nothing inside your cart.</p>}
          {cartItem}
          <div className="fixed bottom-0 py-5 mt-60 w-screen bg-white">
            <div>
              <span>Total Amount: </span>
              <div className="inline">
                <span className="text-sm new-color-title">₱ </span>
                <span className="new-color-title font-bold">{totalAmount}</span>
              </div>
            </div>
            <div className="mt-5 box-content">
              {/* <button
                type="button"
                onClick={toggleShowAndClose}
                data-bs-dismiss="offcanvas"
                aria-label="Close"
                className="border text-sm rounded-full py-1 px-5 mr-2 hover:border-black duration-500"
              >
                Close
              </button> */}
              <Button children="Place order now" />
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default CartPanel;
