import React, { useContext } from "react";
import Card from "../UI/Card";
import MealForm from "./MealForm";
import CartContext from "../../store/CartContext";
import "./MealItem.css";

const MealItem = (props) => {
  //   const mealPrice = `₱${props.price.toFixed(2)}/kg`;
  const CartCtx = useContext(CartContext);
  const addToCartHandler = (amount) => {
    CartCtx.addItem({
      id: props.id,
      image: props.image,
      name: props.name,
      amount: amount,
      price: props.price,
    });
  };
  return (
    <>
      <Card>
        <img
          src={props.image}
          alt="Product Images"
          className="rounded-t-md rounded-b-lg h-44 w-full"
        />
        <div className="px-3 pb-3 pt-1.5">
          <div className="mb-2">
            <h2 className="font-bold text-gray-600 text-lg tracking-wide">
              {props.name}
            </h2>
            <p className="font-semibold new-color-card-title text-sm opacity-85 my-auto">
              ₱ {props.price} /kg
            </p>
          </div>
          <p className="mb-1 text-xs text-black/40">{props.description}</p>
          <hr className="mb-3 mt-1 border-black/5" />
          <div className="mt-2">
            <MealForm id={props.id} onAddToCart={addToCartHandler} />
          </div>
        </div>
      </Card>
    </>
  );
};

export default MealItem;
